import React from "react";
import {
  MDBContainer,
  MDBAccordion,
  MDBAccordionItem,
  MDBIcon,
  MDBRipple
} from "mdb-react-ui-kit";
import { motion } from "framer-motion";

import { questionsData } from "./data";

import "./Questions.css";

const textAnimation = {
  hidden: {
    y: -50,
    opacity: 0,
    transition: {
      duration: 1
    }
  },
  visible: custom => ({
    y: 0,
    opacity: 1,
    transition: {
      delay: custom * 0.2,
      duration: 1
    }
  })
};

export const Questions = () => {
  return (
    <motion.div
      initial="hidden"
      whileInView="visible"
      viewport={{ amount: 0.2, once: true }}>
      <MDBContainer fluid className="questions">
        <h3 className="pt-5 fw-bolder text-center">  </h3>
        <motion.div variants={textAnimation} custom={2}>
          <MDBAccordion
            flush
            initialActive={1}
            className="p-5 questions__wrapper">
            {questionsData.map(question => (
              <MDBRipple key={question.id} className="w-100">
                <MDBAccordionItem
                  collapseId={question.id}
                  className="questions__text"
                  headerTitle={
                    <>
                      <MDBIcon fas icon="tint" className="questions__icon" />
                      {question.title}
                    </>
                  }>
                  {question.content.map((paragraph, index) => (
                    <p key={index}>{paragraph}</p>
                  ))}
                </MDBAccordionItem>
              </MDBRipple>
            ))}
          </MDBAccordion>
        </motion.div>
      </MDBContainer>
    </motion.div>
  );
};

